.basketProduct {
  width: 49%;
  border-radius: 4px;
  display: flex;
  margin-bottom: 6px;
  padding: 10px 0;
}

.basketProduct:nth-of-type(even) {
  margin-right: 6px;
  box-shadow: 2px 2px 4px #d7d7d7;
}

.basketProduct:nth-of-type(odd) {
  box-shadow: -2px 2px 4px #d7d7d7;
}

.basketProductImage {
  width: 150px;
  height: 100%;
  object-fit: cover;
  margin-left: 20px;
}

.basketProductInfo {
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.removeBasketProduct {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: end;
  color: var(--main-color);
  padding-left: 10px;
}

.basketProductName {
  color: var(--main-color);
}

.basketProductColorContainer,
.basketProductSizeContainer {
  display: flex;
}

.basketProductColor,
.basketProductSize {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.basketProductColor {
  background-color: purple;
}

.basketProductSize {
  border: 2px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basketProductPrice {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.basketProductNumber {
  display: flex;
  gap: 10px;
  margin-left: 10px;
  background-color: var(--main-color);
  border-radius: 10px;
  padding: 5px 10px;
}

.basketProductNumber > * {
  width: 33%;
  border: none;
  background-color: var(--main-color);
  color: var(--second-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.basketProductNumber > button:nth-of-type(1) {
  border-radius: 0 6px 6px 0;
}

.basketProductNumber > button:nth-of-type(2) {
  border-radius: 6px 0 0 6px;
}
